//  ()
.mn-float-menu {
	> a {
		position: relative;
		display: block;
		width: calculateRem(43px);
		height: calculateRem(43px);
		margin-top: calculateRem(7px);
		line-height: calculateRem(43px);
		border-radius: 100%;
		border: 1px solid #18af6c;
		background-color: rgba(255,255,255,.9);
		color: #18af6c;
		text-decoration: none;
		font-size: calculateRem(10px);
		text-align: center;
		font-weight: bold;
		letter-spacing: -.08em;
		overflow: hidden;
		box-shadow: 2px 2px 5px rgba(37,178,113,.2);
	}
	.lst_item {
		display:inline-block;
		vertical-align: middle;
		margin-top:-4px;
		line-height: 1.35;
	}
}
//m()
.mn-float-mtimedeal {
	> a {
		position: relative;
		display: block;
		width: calculateRem(43px);
		height: calculateRem(43px);
		margin-top: calculateRem(7px);
		line-height: calculateRem(43px);
		border-radius: 100%;
		background-color: rgba(246,92,74,.9);
		color: #fff;
		text-decoration: none;
		font-size: calculateRem(10px);
		text-align: center;
		font-weight: bold;
		letter-spacing: -.08em;
		overflow: hidden;
		box-shadow: 2px 2px 5px rgba(246,92,74,.2);
		.lst_item {
			display: block;
			width: calculateRem(43px);
			height: calculateRem(43px);
			line-height: calculateRem(43px);
			&:first-child {
				margin-top:1px;
				animation:upper 3s cubic-bezier(1,0,0,1) 3s infinite alternate;
			}
			&:last-child {
				position: relative;
				font-size:0;
				&:after {
					@include btn_mn_mtimedeal();
					position: absolute;
					left:50%;
					top:50%;
					margin:-9px 0 0 -9px;
					content:'';
					vertical-align: middle;
				}
			}
		}
		@keyframes upper {
			0%{margin-top:1px;}
			100%{margin-top:-100%;}
		}
	}
}
//()
.comn-go-prev {	
	> a {
		position: relative;
		display: block;
		width: calculateRem(43px);
		height: calculateRem(43px);
		margin-top: calculateRem(7px);
		line-height: calculateRem(43px);
		border-radius: 100% 7px 100% 100%;
		background-color: rgba(37,178,113,.9);
		color: #fff;
		text-decoration: none;
		font-size: calculateRem(10px);
		text-align: center;
		font-weight: bold;
		overflow: hidden; 
		box-shadow: 2px 2px 5px rgba(37,178,113,.2);
	}
	.lst_item {
		display:inline-block;
		vertical-align: middle;
		margin-top:-2px;
		line-height: 1.35;
	}
}