@charset "UTF-8";
.mn-custom-search {
	position: relative;
	left:0;
	top:0;
	bottom:auto;
	z-index: 3;
	padding:0 23px;
	&.is-fixed {
		position: fixed;
		left:0;
		top:auto;
		bottom:30px;
		width: 100%;		
		transform:translateY(0);
		animation: buttonShow .4s ease-in-out;
		.btn-search-wrap {
			width: 34%;
			margin:0 auto;
			box-shadow: 2px 2px 10px rgba(37, 178, 113, 0.2);
		}
		.btn-search {
			padding:0 12px;
			background: none;
			&:before {
				@include ico_mn_search();
				margin-top:-1px;
			}
			.cus-txt {
				font-size: calculateRem(13px);
				background: none;
				-webkit-background-clip: border-box;
				-webkit-text-fill-color: inherit;
				span {
					display: none;
				}
			} 
		}
	}
	@keyframes buttonShow {
		0% {transform:translateY(62px);}
		100% {transform:translateY(0);}
	}
	.btn-search-wrap {
		height: calculateRem(32px);
		padding:1px;
		background:linear-gradient( 135deg, #43c866 30%, #2fb8c1 70%);
		border-radius: calculateRem(16px);
	}
	.btn-search {
		display: block;
		padding:0 20px;
		height: calculateRem(30px);
		line-height: calculateRem(29px);
		text-align: center;
		font-size: calculateRem(14px);
		font-weight: bold;
		background: #fff;
		color:#fff;
		border-radius: calculateRem(15px);
		overflow: hidden;
		.cus-txt {
			background: linear-gradient(to right, #43c866, #2fb8c1);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
		&:before {
			// @include ico_mn_search();
			@include ico_mn_recent_back();
			content:'';
			margin-right:2px;
			margin-top:-3px;
			vertical-align: middle;
		}
	}
}
.search-custom {
	// position: fixed;
	// top: 0;
	// will-change: top;
	// left:0;
	// display: none;
	// width: 100%;
	// height: 100%;
	// z-index: 100;
	// box-sizing: border-box;
	.pop-wrap {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		display: block;
		width: calc(100% - 20px);
		height:calc(100% - 46px);
		overflow: hidden;
		overflow-y: auto;
		word-break: break-all;
		box-sizing: border-box;
		background: #fff;
		border-radius: 10px;
	} 
	.pop-tit {
		position: relative;
		height:49px;
		line-height:49px;
		text-align: center;
		background:#fff;
		overflow: hidden;
		@include ellipsis;
		.head {
			display: inline-block;
			font-size:calculateRem(15px);
			color:#333;
			font-weight: 700;
		}
		.close {
			position: absolute;
			right:0;
			top:0;
			display: block;
			width:16px;
			height:16px;
			padding:25px 26px;
			cursor: pointer;
			@include txtHidden;
			&:before {
				position: absolute;
				top:50%;
				left:50%;
				content:'';
				margin:0 0 0 -10px;
				width:15px;
				height:1px;
				background:#313333;;
				transform: rotate(-45deg);
			}
			&:after {
				position: absolute;
				top:50%;
				left:50%;
				content:'';
				margin:0 0 0 -10px;
				width:15px;
				height:1px;
				background:#313333;;
				transform: rotate(45deg);
			}
		}
		&:after {
			position: absolute;
			left:0;
			bottom:0;
			display: block;
			width:100%;
			height: 1px;
			content:'';
			background: linear-gradient(140deg, #43c866 0%,#43c866 40%,#2fb8c1 60%,#2fb8c1 100%); 
		}
	}
	.pop-con {
		position: relative;
		height:calc(100% - 49px);
		min-height: 455px;
		padding:28px 23px 69px;
		padding:28px 23px 41px 23px;
		overflow: hidden;
		overflow-y:auto;
		word-break:break-all;
		background-color: #fff;
		-webkit-overflow-scrolling: auto;
		display: flex; 
		justify-content:flex-end; 
		flex-direction:column;
		.sel-box {
			flex:auto;
			display: flex;
			align-items: center;
			height:18%;
			border-top: 1px solid #e6e6e6;
			.sel-box-inner {
				width:100%;
			}
			.qust  {
				display:block;
				height:calculateRem(23px);
				line-height:calculateRem(23px);
				color:#9e9e9e;
				font-size:calculateRem(14px);
				&:after {
					content:'';
					margin-top:-2px;
					margin-left:5px;
					vertical-align: middle;
				}
			}			
			.select-wrap {
				position: relative;
				display: block;
				width: 100%;
				height:calculateRem(23px);
				line-height:calculateRem(23px);
				-webkit-tap-highlight-color:rgba(0,0,0,.1);
				&:after {
					@include btn_mn_more();
					position: absolute;
					right:4px;
					top:50%;
					margin-top:-4px;
					content:'';
					transform:rotate(90deg);
				}
				&.on {
					color:#43c866;
					font-weight: bolder;
					&:after {
						transform: rotate(270deg);
					}
				}

			}
			.select {
				display: block;
				width: 100%;
				height: 100%;
				color:#333;
				padding:0; //IOS  
				font-size:calculateRem(14px);
				font-weight: 600;
				font-family: "Noto Sans KR", Arial, sans-serif;
				border:none;
				&:focus {
					outline:none;
				}
				&.is-change {
					color:#43c866;
					option {
						color:#333;
					}
				}
			}	
			.calendar {
				position: relative;
				display: block;
				width: 100%;
				margin:0 0 3px;
				padding: 0;
				height:calculateRem(28px);
				line-height:calculateRem(22px);
				color:#333;
				font-size:calculateRem(15px);	
				font-weight: bolder;
				border:none;
				-webkit-appearance: none;
				&:after {
					@include btn_mn_more();
					position: absolute;
					right:4px;
					bottom:10px;
					content:'';
					transform:rotate(90deg);
				}
				&.on {
					color:#43c866;
					font-weight: bolder;
					&:after {
						transform: rotate(270deg);
					}
				}
				&:focus {
					outline:none;
				}
				&.is-change {
					color:#43c866;
				}
			}
			&.recent { //
				position: absolute;
				right:23px;
				top:0;
				height: 28px;
				text-align: right;
				justify-content:flex-end;
				border-top:none;
				& + .sel-box {
					border-top:none;
				}
				&.is-open {
					position: static;
					right:auto;
					top:auto;
					.lst-recent-wrap {
						display: block;
					}
					.btn-recent {
						display: none;
					}
					 ~ .sel-box,  ~ .btn-send {
						display: none;
					}
				}
				.btn-recent {
					display:inline-block;
					width: 100%;
					font-size:calculateRem(13px);
					vertical-align: bottom;
					margin-top:10px;
					&:before {
						@include ico_mn_recent();
						content:'';
						margin:-3px 4px 0 0;
						vertical-align: middle;
					}
				}
				.lst-recent-wrap {
					display: none;
					width: 100%;
					height: 100%;
					z-index: 1;
					background:#fff;
					.btn-recent-back {
						position: absolute;
						left: 23px;
						top: 0;
						display:inline-block;
						margin-top:10px;
						font-size:calculateRem(13px);
						&:before {
							@include ico_mn_recent_back();
							content:'';
							margin:-3px 4px 0 0;
							vertical-align: middle;
						}
					}
					.lst-recent {
						position: relative;
						height:100%;
						&:before {
							position: absolute;
							left:0;
							top:0;
							display: block;
							width:100%;
							height: 90px;
							line-height: 90px;
							color:#aaa;
							font-size: calculateRem(13px);
							text-align: center;
							content:'   .';
						}
						li {
							position: relative;
							margin-top:10px;
							padding:0 14px;
							// height: calc(20% - 10px);
							height:72px;
							border:1px solid #e6e6e6;
							border-radius: 2px;
							background:#fff;
						}
						.link {		
							display: flex;
							height: 100%;
							justify-content:center;
							flex-direction: column;
							text-align: left;
							overflow: hidden;
						}
						.btn-remove {
							position: absolute;
							right:0;
							top:0;
							padding:12px 12px 0 0;
							width:40px;
							height:40px;
							text-align: right;
							vertical-align: top;
							&:before {
								content:'';
								@include btn_mn_recent_close();
								vertical-align: top;
							}
						}
						.departure {
							margin:0 0 1px;
							color:#979797;
							font-size: calculateRem(12px);
						}
						.destination {
							margin:0 0 1px;
							font-weight: bold;
							font-size: calculateRem(14px);
							.loca {
								margin-right: 6px;;
								color:#43c866;
							}
							.item {
								color:#333;
							}
						}
						.option {
							font-size: calculateRem(13px);
							span {
								&:before {
									display:inline-block;
									content:'';
									width:1px;
									height:10px;
									margin:0 9px 0 5px;
									background:#d2d2d2;
								}
								&:first-child {
									&:before{
										content:none;
									}
								}
							}
						}
					}
				}
			}
			&.search { //?
				// position: relative;
				height:36%;
				& + .sel-box {
					border-top: none;
				}
				.input-wrap {
					position: relative;
					height:calculateRem(36px);
					z-index: 3;
					&:after {
						position: absolute;
						left:0;
						bottom:0;
						display: block;
						width:100%;
						height:1px;
						background:#e6e6e6;
						content:'';
					}
				}
				.input {
					width:calc(100% - 21px);
					height: 100%;
					font-size:calculateRem(14px);
					font-family: "Noto Sans KR", Arial, sans-serif;
					border:none;
					padding:0; //IOS  
					background: transparent;
					vertical-align: middle;
					&:focus {
						outline:none;
						font-weight: 600;
						&::-webkit-input-placeholder {
							font-weight:normal;
						}
					}
					&.is-change {
						color:#43c866;
						font-weight: 600;
					}
					&::-webkit-input-placeholder {
						font-weight:normal;
						color:#bebebe;
					}
				}

				//
				.popular {
					height:74px;
					padding-top:9px;
					margin-top: 14px;
					margin-bottom:-5%;
					background: #f6f6f6;
					border-radius: 3px;
					overflow: hidden;
					& + .lst-result-wrap {
						top:39%;	
						height: 50%;				
					}
					.swiper-container {
						width: 100%;
					}
					.tit {
						height: 1.4375rem;
						line-height: 1.4375rem;
						padding-left: 15px;
						margin-bottom: 3px;
						font-size: calculateRem(12px);
						color: #9e9e9e; 
						&:before {
							margin-right: 5px;
							content:'';
							@include ico_mn_city();
						}
					}
					.lst-city {
						li {
							display: inline-block !important;
							width:auto;
							margin-right: 10px;
							border-radius: 20px;
							overflow:hidden;
							&:first-child {
								margin-left: 15px;
							}
							&:last-child {
								margin-right: 15px;
							}
						}
						a {
							display: block;
							height:23px;
							line-height:23px;
							padding:0 10px;
							font-size: calculateRem(12px);
							color: #333;
							background:#fff;
						}
					}
				} 
				.lst-result-wrap {
					position: absolute;
					left:23px;
					top: 45.5%;
					display: none;
					width:calc(100% - 46px);
					height: 45%;
					z-index: 2;
					&.is-open {
						display: block;
					}
				}		
				.lst-result {
					position: absolute;
					left:0;
					top: 0;
					width:100%;
					height: 100%;
					background:#fff;
					overflow: hidden;
					overflow-y: auto;
					li {
						height:10%;
						font-size:calculateRem(14px);
						@include ellipsis();
					}
					a {
						display: block;
						width:100%;
						height: 100%;
						// padding-top: 3px;
						&:focus, &:hover {
							font-weight:bold;
						}
						* {
							vertical-align: middle;

						}
					}
					span {
						&:after {
							content:'';
							margin:0 10px;
							@include btn_mn_more();
						}
						&:last-child {
							color:#43c866;
							&:after {
								content:none;
							}

						}
					}
				}
				.btn-srch {
					position: absolute;
					right:0;
					top:50%;
					margin-top:-10px;
					display: block;
					border:none;
					padding-right:0;
					padding-left:10px;
					&:before {
						@include ico_mn_search2();
						content:'';
						margin-top:-1px;
						vertical-align: middle;
					}
					&:focus {
						outline:none;
					}
				}
			}
		}
	}
	.btn-send {
		position: absolute;
		left:0;
		bottom:-1px;
		width:100%;
		button, a {
			display:block;
			height:100%;
			line-height:41px;
			width:100%;
			color:#fff;
			text-align: center;
			font-size:calculateRem(14px);
			background: linear-gradient(140deg, #43c866 0%,#43c866 40%,#2fb8c1 60%,#2fb8c1 100%); 
		}
	}
}
	
