@function calculateRem($size) { //rem 
	$remSize: $size / 16px;
	@return #{$remSize}rem;
}

//1 
@mixin ellipsis() {
	overflow:hidden; 
	white-space:nowrap; 
	text-overflow:ellipsis; 
}

@mixin txtHidden() {
	overflow:hidden; 
   display:inline-block; 
   text-indent:-9999px; 
   white-space:nowrap; 
   line-height:0;
}